import Vue from 'vue'
import VueI18n from 'vue-i18n'
import LangStorage from "./lang"

Vue.use(VueI18n)

const i18n = new VueI18n({
  // 
  locale: LangStorage.getLang('zh'),
  // locale: 'zh', // 
  messages: {
    'zh': require('./zh'),
    'en': require('./en')
  }
})

export default i18n
